import RedGPUContextBase from "../../../context/RedGPUContextBase";
/**
 * IndexBuffer
 */
class IndexBuffer extends RedGPUContextBase {
    #gpuBuffer;
    #indexNum;
    #byteLength;
    #descriptor;
    #usage;
    /**
     * IndexBuffer
     */
    constructor(redGPUContext, data, usage = GPUBufferUsage.INDEX | GPUBufferUsage.COPY_DST) {
        super(redGPUContext);
        this.#indexNum = data.length;
        this.#byteLength = data.byteLength;
        this.#usage = usage;
        this.#descriptor = {
            size: data.byteLength,
            usage: this.#usage
        };
        this.#gpuBuffer = this.redGPUContext.gpuDevice.createBuffer(this.#descriptor);
        this.update(0, data);
        // console.log(this)
    }
    get gpuBuffer() {
        return this.#gpuBuffer;
    }
    get indexNum() {
        return this.#indexNum;
    }
    get byteLength() {
        return this.#byteLength;
    }
    get descriptor() {
        return this.#descriptor;
    }
    get usage() {
        return this.#usage;
    }
    update(gpuBufferOffset, data, dataOffset, size) {
        this.redGPUContext.gpuDevice.queue.writeBuffer(this.#gpuBuffer, gpuBufferOffset, data, dataOffset, size);
    }
    destroy() {
        //TODO
    }
}
export default IndexBuffer;
