import LightManager from "../../light/LightManager";
import DisplayContainer from "../../object3d/base/DisplayContainer";
import hexadecimalToRgb from "../../util/color/hexadecimalToRgb";
let UUID = 0;
/**
 * @description
 * -   Scene .
 * -       .
 */
class Scene extends DisplayContainer {
    /**
     * Scene axis 
     * @private
     */
    #axis;
    /**
     * Scene skyBox 
     * @private
     */
    #skyBox;
    /**
     * Scene grid 
     * @private
     */
    #grid;
    /**
     * Scene label 
     * @private
     */
    #label;
    /**
     * lightManager
     * Scene  Light    
     * @private
     */
    #lightManager;
    #backgroundColorRGB = [0, 0, 0];
    /**
     * Scene  
     * @private
     */
    #backgroundColor = 0x000000;
    /**
     * Scene    
     * @private
     */
    #backgroundAlpha = 1;
    /**
     * @constructor  Scene  .
     * @param label
     */
    constructor(label) {
        super();
        this.#label = label || `Scene${UUID++}`;
        this.#lightManager = new LightManager();
    }
    /**
     * Scene  axis   .
     */
    get axis() {
        return this.#axis;
    }
    /**
     * Scene Axis  .
     * @param value
     */
    set axis(value) {
        this.#axis = value;
    }
    /**
     * Scene  skybox   .
     */
    get skyBox() {
        return this.#skyBox;
    }
    /**
     * Scene SkyBox  .
     * @param value
     */
    set skyBox(value) {
        this.#skyBox = value;
    }
    /**
     * Scene  grid   .
     */
    get grid() {
        return this.#grid;
    }
    /**
     * Scene Grid  .
     * @param value
     */
    set grid(value) {
        this.#grid = value;
    }
    /**
     * Scene label  .
     */
    get label() {
        return this.#label;
    }
    /**
     * Scene label .
     * @param value
     */
    set label(value) {
        this.#label = value;
    }
    /**
     * lightManager 
     */
    get lightManager() {
        return this.#lightManager;
    }
    /**
     * backgroundColor   RGB 
     * GPU  
     */
    get backgroundColorRGB() {
        return this.#backgroundColorRGB;
    }
    /**
     * Scene   
     */
    get backgroundColor() {
        return this.#backgroundColor;
    }
    /**
     * Scene      .
     *  Scene   .
     *     backgroundColorRGB  .
     * ex) 0xffffff
     * @param value
     */
    set backgroundColor(value) {
        this.#backgroundColor = value;
        let rgb = hexadecimalToRgb(value);
        this.#backgroundColorRGB[0] = rgb.r;
        this.#backgroundColorRGB[1] = rgb.g;
        this.#backgroundColorRGB[2] = rgb.b;
    }
    /**
     * Scene     .
     *  Scene   
     * ```ex) backgroundColor 0x000000  backgroundAlpha  0.5  rgba(0,0,0,0.5)  .```
     */
    get backgroundAlpha() {
        return this.#backgroundAlpha;
    }
    /**
     * Scene     .
     * @param value
     */
    set backgroundAlpha(value) {
        this.#backgroundAlpha = value;
    }
}
export default Scene;
