/**
 * DisplayContainer /    .
 */
class DisplayContainer {
    /**
     *  
     * @private
     */
    #children = [];
    /**
     *  
     * @private
     */
    #parent;
    constructor() {
    }
    /**
     *   .
     */
    get children() {
        return this.#children;
    }
    /**
     *    .
     */
    get parent() {
        return this.#parent;
    }
    /**
     *   .
     * @param value
     */
    set parent(value) {
        this.#parent = value;
    }
    /**
     *   .
     * *   parent    parent  .
     * @param child
     */
    addChild(child) {
        if (child.parent)
            child.parent.remove(child);
        child.parent = this;
        this.#children.push(child);
        return this;
    }
    /**
     *   index    .
     * *   parent    parent  .
     * * index children     push.
     * @param child
     * @param index
     */
    addChildAt(child, index) {
        if (child.parent)
            child.parent.remove(child);
        child.parent = this;
        if (this.#children.length < index)
            index = this.#children.length;
        this.#children.splice(index, 0, child);
        return this;
    }
    /**
     *   .
     * *      .
     * @param child
     */
    removeChild(child) {
        const index = this.#children.indexOf(child);
        if (index > -1) {
            child.parent = null;
            this.#children.splice(index, 1);
        }
        return this;
    }
    /**
     * index    .
     * @param index
     */
    removeChildAt(index) {
        const child = this.#children[index];
        if (child) {
            child.parent = null;
            this.#children.splice(index, 1);
        }
        return this;
    }
    /**
     *    .
     */
    removeAllChild() {
        let i = this.#children.length;
        while (i--) {
            this.#children[i].parent = null;
        }
        this.#children.length = 0;
        return this;
    }
}
export default DisplayContainer;
